.shopping-list-page {
    width: 100%;
    padding-top: 40px;
    display: block;
}
.shopping-main-title {
  font-weight: 700;
  font-size: 32px;
  line-height: 1.19;
display: block;
  margin-bottom: 40px;

  @media screen and (min-width: 768px) {
    font-size: 48px;
    line-height: 1.08;
  }
}

.shopping-list-color {
  color: #4F2EE8;
}

.shopping-list-item {
  border: 2px solid rgba(79, 46, 232, 0.4);
  border-radius: 16px;

  padding: 20px;
  max-width: 335px;
  margin-bottom: 20px;

  position: relative;

  @media screen and (min-width: 768px) {
    padding: 24px;
    max-width: none;
    width: 704px;
  }
  @media screen and (min-width: 1440px) {
    width: 996px;
  }
}

.shopping-list-item:last-child {
  margin-bottom: 0;
}

.shopping-button {
  position: absolute;
  top: 14px;
  right: 14px;

  width: 28px;
  height: 28px;

  border-radius: 50%;
  padding: 0;

  background-color: #4F2EE8;
  border: none;

  display: flex;
  align-items: center;
  justify-content: center;

  @media screen and (min-width: 768px) {
    top: 24px;
    right: 24px;

    width: 34px;
    height: 34px;
  }
}

.shopping-button-icon {
  pointer-events: none;
}

.shopping-box {
  margin: 0;
}

.shopping-img {
  flex-shrink: 0;
  max-width: 98px;
  max-height: 139px;

  margin-right: 20px;
  margin-bottom: 4px;

  @media screen and (min-width: 768px) {
    width: none;
    height: none;
    margin-right: 24px;
    margin-bottom: 0;
  }
}

.book-title {
  font-weight: 700;
  font-size: 16px;
  line-height: 1.12;

  @media screen and (max-width: 767px) {
    max-width: 144px;
  }

  margin-bottom: 4px;
}

.book-genre {
  font-size: 12px;
  line-height: 1.17;
  letter-spacing: 0.03em;

//   color: #B4AFAF;

  margin-bottom: 24px;

  @media screen and (min-width: 768px) {
    margin-bottom: 14px;
  }
}

.book-author {
  font-style: italic;
  font-size: 12px;
  line-height: 1.17;

//   color: #B4AFAF;

  @media screen and (max-width: 767px) {
    max-width: 98px;
  }
}

.shops-links-list {
  display: flex;
  align-items: center;
  gap: 14px;
  margin-left: auto;
}

.shops-list-icon {
  mix-blend-mode: luminosity;
  transition: filter 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.shop-list-icon:hover,
.shop-list-icon:focus {
  filter: drop-shadow(0px 3px 3px rgba(0, 0, 0, 0.15));
  mix-blend-mode: luminosity;
}

.book-description {
  font-size: 14px;
  line-height: 1.28;
}

.visually-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}

.empty-list-js {
  display: flex;
  flex-direction: column;
  align-items: center;

  margin-top: 80px;

  @media screen and (min-width: 768px) {
    margin-top: 100px;
  }
}

.empty-list-text {
  text-align: center;
  max-width: 349px;

  font-size: 18px;
  line-height: 1.33;

  margin-bottom: 20px;
}

.empty-list-img {
  width: 265px;
  height: 198px;

  @media screen and (min-width: 768px) {
    width: 322px;
    height: 241px;
  }
}