// ==========МОБІЛЬНА ВЕРСІЯ========//

.header {
  padding-top: 17px;
  padding-bottom: 16px;

  border: 1.5px solid #111111;
  border-radius: 0px 0px 8px 8px;

  background-color: var(--light-header-bg-color);
}
@media screen and (max-width: 767px) {
  .header {
    position: sticky;
    width: 100vw;
    z-index: 100;
    top: 0;
    right: 0;
  }
}
.header-container {
  display: flex;
  align-items: center;
}
.logo-link {
  display: flex;
  align-items: center;
  gap: 8px;
  letter-spacing: -0.04em;

  color: var(--light-nav-text-color);
  font-weight: 500;
}
.burger-menu {
  background-color: transparent;
  border: none;
  line-height: 0;
  padding: 0;
}
.burger-logo {
  stroke: black;
}
.navigation {
  display: none;
}
.user-login {
  display: none;
}
// ==========ПЛАНШЕНТА ВЕРСІЯ==========//
@media screen and (min-width: 768px) {
  .header {
    padding-top: 11px;
    padding-bottom: 10px;
  }
}

@media screen and (min-width: 768px) {
  .burger-menu {
    display: none;
  }
  .navigation {
    display: block;
  }
  .mobile-menu {
    display: none;
  }
  .logo-link {
    margin-right: 28px;
  }
}

@media screen and (min-width: 768px) {
  .nav-list {
    display: flex;
    align-items: center;
    gap: 8px;
  }
}
@media screen and (min-width: 768px) {
  .home-link {
    padding: 8px 16px;
    display: inline-flex;
    font-size: 14px;
    line-height: 1.28;
    letter-spacing: -0.04em;
    font-weight: 500;

    border-radius: 36px;
    color: var(--light-border-n-text-color);
    transition: background-color 500ms linear;
  }
  .home-link:hover,
  .home-link:hover {
    background-color: #eac645;
  }
}
@media screen and (min-width: 768px) {
  .shopping-link {
    padding: 7px 16px;
    display: inline-flex;
    align-items: center;
    gap: 8px;
    font-size: 14px;
    font-weight: 500;

    line-height: 1.28;
    letter-spacing: -0.04em;
    border-radius: 36px;

    color: var(--light-border-n-text-color);
    transition: 500ms linear;
  }
  .shopping-link:hover,
  .shopping-link:hover {
    background: #eac645;
  }
  .shopping-bag-icon {
    line-height: 0;
  }
}

@media screen and (min-width: 768px) {
  .user-login {
    width: 184px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 13px 14px 12px;
    background: var(--dark-accent-color);
    cursor: pointer;

    font-weight: 500;
    font-size: 14px;
    line-height: 1.29;
    letter-spacing: -0.04em;

    color: var(--dark-border-n-text-color);
    border: 1.5px solid #111111;
    border-radius: 18px;

    // font-weight: 700;
    // font-size: 22px;
    // letter-spacing: -0.02em;
    // border-radius: 36px;
    // border: transparent;
  }

  .icon-arrow-right {
    stroke: #eac645;
  }
  .icon-user-wrapper {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin-right: 9px;
    width: 37px;
    height: 37px;
    background: #f6f6f6;
    border-radius: 50%;
  }
  .icon-registration-triangle {
    margin-left: 4px;
  }
}

