.auth-backdrop {
  padding-top: 96px;
  padding-left: 20px;
  padding-right: 20px;

  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;

  background-color: var(--light-accent-color);

  opacity: 1;

  overflow-y: auto;
  z-index: 999;
}

.auth-backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.auth-form {
  position: relative;
  margin: 0;

  max-width: 335px;

  display: block;

  background-color: var(--light-header-bg-color);
  border: 2px solid var(--light-border-n-text-color);
  border-radius: 18px;
  @media screen and (min-width: '579px') {
    max-width: 579px;
    width: 100%;

    margin-left: auto;
    margin-right: auto;
  }
}

body.auth-form-open {
  overflow: hidden;
}

.auth-content {
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 40px;
  padding-top: 40px;

  display: flex;
  flex-direction: column;
  @media screen and (min-width: '579px') {
    padding-top: 80px;
  }
}

.auth-form-close-btn {
  position: absolute;
  top: 14px;
  right: 14px;
  width: 20px;
  height: 20px;

  display: block;

  @media screen and (min-width: '579px') {
    top: 24px;
    right: 24px;

    width: 28px;
    height: 28px;
  }
}

.auth-form-cross-icon {
  stroke: var(--light-border-n-text-color);
  @media screen and (min-width: '579px') {
    width: 28px;
    height: 28px;
  }
}

.auth-content input {
  padding-top: 16px;
  padding-bottom: 16px;
  padding-left: 24px;
  margin-bottom: 24px;

  border: 2px solid var(--light-border-n-text-color);
  border-radius: 40px;

  @media screen and (min-width: 579px) {
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 40px;
    margin-bottom: 28px;
  }
}

.auth-content input::placeholder,
.signup-btn {
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.12;
  letter-spacing: -0.01em;
  text-transform: uppercase;

  @media screen and (min-width: 579px) {
    font-size: 18px;
    line-height: 1.33;
  }
}

.auth-form-links {
  display: flex;
  justify-content: center;

  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.29;

  letter-spacing: -0.01em;
  text-transform: uppercase;
}

.auth-form-links a {
  color: var(--light-accent-color);
}

.auth-form-links a:not(:last-child) {
  margin-right: 14px;
}

.auth-content input::placeholder {
  color: var(--light-border-n-text-color);
}

.signup-btn {
  padding-top: 20px;
  padding-bottom: 20px;
  margin-bottom: 14px;

  color: var(--light-header-bg-color);

  background-color: var(--light-border-n-text-color);
  border: transparent;
  border-radius: 40px;
}
